<!-- App.vue -->
<template>
  <el-container class="layout-container">
    <el-aside width="220px" class="sidebar">
      <div class="logo">
        <el-icon size="30" color="#409EFF"><Setting /></el-icon>
        <span class="logo-title"></span>
      </div>
      <el-menu
        :default-active="$route.path"
        class="sidebar-menu"
        :router="true"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#409EFF"
      >
        <el-menu-item index="/" :route="{ name: 'home' }">
          <el-icon><House /></el-icon>
          <span></span>
        </el-menu-item>
        <el-menu-item index="/users" :route="{ name: 'users' }" v-if="has_permission('user')">
          <el-icon><UserFilled /></el-icon>
          <span></span>
        </el-menu-item>
        <el-menu-item index="/role" :route="{ name: 'roles' }" v-if="has_permission('user')">
          <el-icon><User /></el-icon>
          <span></span>
        </el-menu-item>
        <el-menu-item index="/password" :route="{ name: 'password' }">
          <el-icon><Postcard /></el-icon>
          <span></span>
        </el-menu-item>
        <el-menu-item index="/banners" :route="{ name: 'banners' }" v-if="has_permission('banner')">
          <el-icon><Picture /></el-icon>
          <span></span>
        </el-menu-item>
        <el-menu-item index="/links" :route="{ name: 'links' }" v-if="has_permission('link')">
          <el-icon><Link /></el-icon>
          <span></span>
        </el-menu-item>
        
        <el-sub-menu index="/tools" v-if="has_permission('tool')">
          <template #title>
            <el-icon><Tools /></el-icon>
            <span></span>
          </template>
          <el-menu-item index="/tools/list" :route="{ name: 'toolmanagement' }" ></el-menu-item>
          <el-menu-item index="/tools/type" :route="{ name: 'tooltype' }"></el-menu-item>
        </el-sub-menu>

        <el-sub-menu index="/announcements" v-if="has_permission('announce')">
          <template #title>
            <el-icon><Bell /></el-icon>
            <span></span>
          </template>
          <el-menu-item index="/announcements/list" :route="{ name: 'announcelist' }"></el-menu-item>
          <el-menu-item index="/announcements/add" :route="{ name: 'announceadd' }"></el-menu-item>
        </el-sub-menu>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header class="header">
        <div class="header-left">
          <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item>{{ currentPageTitle }}</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="header-right">
          <el-dropdown>
            <div class="user-info">
              <el-avatar
                size="small"
                src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"
              />
              <span class="username">{{ this.$auth.user.username }}{{ $auth.user.role.name }}</span>
            </div>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="logout">
                  <el-icon><SwitchButton /></el-icon>
                </el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </el-header>

      <el-main class="main-content">
        <router-view />
      </el-main>
      
      <el-footer class="footer">
        <p> 2025  -  Element Plus </p>
      </el-footer>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "App",
  computed: {
    currentPageTitle() {
      const routeMap = {
        '/': '',
        '/users': '',
        '/banners': '',
        '/links': '',
        '/tools/list': '',
        '/tools/type': '',
        '/announcements/list': '',
        '/announcements/add': ''
      }
      return routeMap[this.$route.path] || ''
    }
  },
  mounted() {
    if(!this.$auth.is_active){
      window.location.href = this.$http.server_host;
    }
  },
  methods: {
    has_permission(permission){
      return this.$auth.user.permissions.indexOf(permission) >= 0
    },
    logout() {
      this.$auth.clearUserToken();
      window.location.href = this.$http.server_host + "/login";
    }
  }
}
</script>

