<!-- UserManagement.vue -->
<template>
  <div class="user-management">
    <!--  -->
    <div class="toolbar">
      <el-button 
        type="primary" 
        @click="openAddDialog"
        v-if="canAddUser"
      >
        
      </el-button>
    </div>

    <!--  -->
    <el-table 
      :data="userList" 
      style="width: 100%" 
      v-loading="loading"
      stripe
    >
      <el-table-column prop="username" label="" width="150" />
      <el-table-column prop="realname" label="" width="150" />
      <el-table-column prop="role.name" label="" width="150">
        <template #default="scope">
          <el-tag :type="getRoleTagType(scope.row.role)">
            {{ scope.row.role.name }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template #default="scope">
          <el-tag :type="scope.row.is_active ? 'success' : 'danger'">
            {{ scope.row.is_active ? '' : '' }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="join_time" label="" width="200">
        <template #default="scope">
          {{ formatDate(scope.row.join_time) }}
        </template>
      </el-table-column>
      <el-table-column label="" width="200">
        <template #default="scope">
          <el-button
            v-if="canModifyUser(scope.row)"
            size="small"
            :type="scope.row.is_active ? 'danger' : 'success'"
            @click="toggleUserStatus(scope.row)"
          >
            {{ scope.row.is_active ? '' : '' }}
          </el-button>
          <el-button
            v-if="canDeleteUser(scope.row)"
            size="small"
            type="danger"
            @click="openDeleteDialog(scope.row)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <el-pagination
        v-if="total > 0"
        @current-change="handlePageChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="total"
      />
    </div>

    <!--  -->
    <el-dialog
      title=""
      v-model="addUserDialogVisible"
      width="500px"
      @close="resetAddUserForm"
    >
      <el-form
        :model="addUserForm"
        :rules="addUserRules"
        ref="addUserFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addUserForm.username" placeholder="" />
        </el-form-item>
        
        <el-form-item label="" prop="password">
          <el-input
            v-model="addUserForm.password"
            type="password"
            placeholder=""
            show-password
          />
        </el-form-item>
        
        <el-form-item label="" prop="realname">
          <el-input v-model="addUserForm.realname" placeholder="" />
        </el-form-item>
        
        <el-form-item label="" prop="role_id">
          <el-select 
            v-model="addUserForm.role_id" 
            placeholder="" 
            style="width: 100%"
          >
            <el-option
              v-for="role in roleList"
              :key="role.id"
              :label="role.name"
              :value="role.id"
            />
          </el-select>
        </el-form-item>
      </el-form>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="addUserDialogVisible = false"></el-button>
          <el-button type="primary" @click="submitAddUser"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      v-model="deleteUserDialogVisible"
      width="30%"
      center
    >
      <div class="delete-confirm-content">
        <el-icon class="delete-icon"><Warning /></el-icon>
        <span> "{{ currentUserToDelete?.username }}" </span>
      </div>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="deleteUserDialogVisible = false"></el-button>
          <el-button type="danger" @click="confirmDeleteUser"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "UserManagement",
  data() {
    return {
      loading: false,
      userList: [],
      roleList: [],
      total: 0,
      currentPage: 1,
      pageSize: 10,
      addUserDialogVisible: false,
      deleteUserDialogVisible: false,
      currentUserToDelete: null,
      addUserForm: {
        username: '',
        password: '111111',
        realname: '',
        role_id: ''
      },
      addUserRules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        realname: [
          { required: true, message: '', trigger: 'blur' }
        ],
        role_id: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    };
  },
  computed: {
    // 
    currentUser() {
      return this.$auth.user;
    },
    
    // 
    currentUserRoleLevel() {
      return this.$auth.user.role.permissions;
    },
    
    // 
    maxRoleLevel() {
      if (this.roleList.length === 0) return 0;
      return Math.max(...this.roleList.map(role => role.permissions));
    },
    
    // 
    secondMaxRoleLevel() {
      if (this.roleList.length < 2) return 0;
      const permissions = [...new Set(this.roleList.map(role => role.permissions))]
        .sort((a, b) => b - a);
      return permissions.length > 1 ? permissions[1] : permissions[0];
    },
    
    // 
    canAddUser() {
      return this.currentUserRoleLevel >= this.secondMaxRoleLevel;
    }
  },
  mounted() {
    this.loadUserList();
    this.loadRoleList();
  },
  methods: {
    // 
    async loadUserList() {
      this.loading = true;
      try {
        const response = await this.$http.getUserList(this.currentPage);
        if (response.code === 200) {
          this.userList = response.data.user_list || [];
          this.total = response.data.total_count || 0;
        } else {
          this.$message.error(response.message || '');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      } finally {
        this.loading = false;
      }
    },
    
    // 
    async loadRoleList() {
      try {
        const response = await this.$http.getRoleList();
        if (response.code === 200) {
          this.roleList = response.data || [];
        } else {
          this.$message.error(response.message || '');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },
    
    // 
    handlePageChange(page) {
      this.currentPage = page;
      this.loadUserList();
    },
    
    // 
    formatDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
    },
    
    // 
    getRoleTagType(role) {
      if (!role) return 'info';
      
      const roleLevel = role.permissions;
      
      if (roleLevel === this.maxRoleLevel) {
        return 'danger'; // 
      } else if (roleLevel === this.secondMaxRoleLevel) {
        return 'warning'; // 
      } else if (roleLevel === Math.min(...this.roleList.map(r => r.permissions))) {
        return 'info'; // 
      } else {
        return 'primary'; // 
      }
    },
    
    // 
    openAddDialog() {
      this.addUserDialogVisible = true;
    },
    
    // 
    resetAddUserForm() {
      this.addUserForm = {
        username: '',
        password: '111111',
        realname: '',
        role_id: ''
      };
      if (this.$refs.addUserFormRef) {
        this.$refs.addUserFormRef.resetFields();
      }
    },
    
    // 
    submitAddUser() {
      this.$refs.addUserFormRef.validate(async (valid) => {
        if (valid) {
          try {
            const response = await this.$http.addUser(this.addUserForm);
            if (response.code === 200) {
              this.$message.success('');
              this.addUserDialogVisible = false;
              this.resetAddUserForm();
              this.loadUserList();
            } else {
              this.$message.error(response.message || '');
            }
          } catch (error) {
            console.error(':', error);
            this.$message.error('');
          }
        }
      });
    },
    
    // 
    async toggleUserStatus(user) {
      try {
        const newStatus = user.is_active ? 0 : 1;
        const response = await this.$http.activeUser(user.id, newStatus);
        if (response.code === 200) {
          this.$message.success(`${!user.is_active ? '' : ''}`);
          this.loadUserList();
        } else {
          this.$message.error(response.message || `${!user.is_active ? '' : ''}`);
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },
    
    // 
    openDeleteDialog(user) {
      this.currentUserToDelete = user;
      this.deleteUserDialogVisible = true;
    },
    
    // 
    async confirmDeleteUser() {
      try {
        const response = await this.$http.deleteUser(this.currentUserToDelete.id);
        if (response.code === 200) {
          this.$message.success('');
          this.deleteUserDialogVisible = false;
          this.currentUserToDelete = null;
          this.loadUserList();
        } else {
          this.$message.error(response.message || '');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },
    
    // /
    canModifyUser(user) {
      // 
      if (user.id === this.currentUser.id) {
        return false;
      }
      
      const userRoleLevel = user.role?.permissions || 0;
      
      // 
      if (this.currentUserRoleLevel === this.maxRoleLevel) {
        return true;
      }
      
      // 
      if (this.currentUserRoleLevel === this.secondMaxRoleLevel) {
        return userRoleLevel < this.secondMaxRoleLevel;
      }
      
      // 
      return userRoleLevel < this.currentUserRoleLevel;
    },
    
    // 
    canDeleteUser(user) {
      // 
      if (user.id === this.currentUser.id) {
        return false;
      }
      
      const userRoleLevel = user.role?.permissions || 0;
      
      // 
      if (this.currentUserRoleLevel === this.maxRoleLevel) {
        return true;
      }
      
      // 
      if (this.currentUserRoleLevel === this.secondMaxRoleLevel) {
        return userRoleLevel < this.secondMaxRoleLevel;
      }
      
      // 
      return userRoleLevel < this.currentUserRoleLevel;
    }
  }
};
</script>

<style scoped>
.user-management {
  padding: 20px;
}

.toolbar {
  margin-bottom: 20px;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.delete-confirm-content {
  display: flex;
  align-items: center;
  justify-content: center;
}

.delete-icon {
  font-size: 24px;
  color: #F56C6C;
  margin-right: 10px;
}

.dialog-footer {
  display: flex;
  justify-content: center;
}

.dialog-footer .el-button {
  margin: 0 10px;
}
</style>