<!-- ToolType.vue -->
<template>
  <div class="tool-type">
    <!--  -->
    <div class="toolbar">
      <el-button type="primary" @click="openAddDialog"></el-button>
    </div>

    <!--  -->
    <el-table :data="toolTypeList" style="width: 100%" v-loading="loading">
      <el-table-column prop="name" label="" />
      <el-table-column prop="priority" label="" />
      <el-table-column label="">
        <template #default="scope">
          <span v-html="scope.row.icon"></span>
        </template>
      </el-table-column>
      <el-table-column prop="create_time" label="">
        <template #default="scope">
          {{ formatDate(scope.row.create_time) }}
        </template>
      </el-table-column>
      <el-table-column label="" width="150">
        <template #default="scope">
          <el-button
            size="small"
            type="danger"
            @click="openDeleteDialog(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog
      title=""
      v-model="dialogVisible"
      width="500px"
      @close="resetForm"
    >
      <el-form
        :model="toolTypeForm"
        :rules="formRules"
        ref="toolTypeFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="toolTypeForm.name" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="priority">
          <el-input-number
            v-model="toolTypeForm.priority"
            :min="0"
            controls-position="right"
          />
        </el-form-item>
        <el-form-item label="" prop="icon">
          <el-select v-model="toolTypeForm.iconKey" placeholder="">
            <el-option
              v-for="item in iconOptions"
              :key="item.key"
              :label="item.label"
              :value="item.key"
            >
              <span v-html="item.html"></span>
              <span style="margin-left: 10px">{{ item.label }}</span>
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="submitForm"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog title="" v-model="deleteToolTypeVisible" width="300px" center>
      <span></span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="deleteToolTypeVisible = false"></el-button>
          <el-button type="danger" @click="confirmToolTypeDelete"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ToolType",
  data() {
    return {
      loading: false,
      toolTypeList: [],
      dialogVisible: false,
      deleteToolTypeVisible: false,
      currentToolType: null,
      toolTypeForm: {
        name: "",
        priority: 0,
        iconKey: "",
      },
      formRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        priority: [
          { required: true, message: "", trigger: "blur" },
        ],
        iconKey: [{ required: true, message: "", trigger: "change" }],
      },
      iconOptions: [
        {
          key: "star",
          label: "",
          html: '<i class="fas fa-star me-2 text-warning"></i>',
        },
        {
          key: "heart",
          label: "",
          html: '<i class="fas fa-heart me-2 text-danger"></i>',
        },
        {
          key: "cloud",
          label: "",
          html: '<i class="fas fa-cloud me-2 text-info"></i>',
        },
        {
          key: "code",
          label: "",
          html: '<i class="fas fa-code me-2 text-danger"></i>',
        },
        {
          key: "cog",
          label: "",
          html: '<i class="fas fa-cog me-2 text-secondary"></i>',
        },
        {
          key: "chart",
          label: "",
          html: '<i class="fas fa-image me-2 text-success"></i>',
        },
        {
          key: "user",
          label: "",
          html: '<i class="fas fa-user me-2 text-primary"></i>',
        },
        {
          key: "file",
          label: "",
          html: '<i class="fas fa-file-alt me-2 text-primary"></i>',
        },
        {
          key: "qrcode",
          label: "",
          html: '<i class="fas fa-qrcode me-2 text-secondary"></i>',
        },
      ],
    };
  },
  mounted() {
    this.loadToolTypeList();
  },
  methods: {
    // 
    async loadToolTypeList() {
      this.loading = true;
      try {
        const response = await this.$http.getToolTypeList();
        if (response.code === 200) {
          if (response.data.length > 0) {
            this.toolTypeList = response.data;
          } else {
            this.toolTypeList = [];
          }
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        this.$message.error("");
      } finally {
        this.loading = false;
      }
    },

    // 
    openAddDialog() {
      this.dialogVisible = true;
    },

    // 
    resetForm() {
      this.toolTypeForm = {
        name: "",
        priority: 0,
        iconKey: "",
      };
      this.currentToolType = null;
      if (this.$refs.toolTypeFormRef) {
        this.$refs.toolTypeFormRef.resetFields();
      }
    },

    // 
    submitForm() {
      this.$refs.toolTypeFormRef.validate(async (valid) => {
        if (valid) {
          try {
            // HTML
            const selectedIcon = this.iconOptions.find(
              (option) => option.key === this.toolTypeForm.iconKey
            );
            const formData = {
              name: this.toolTypeForm.name,
              priority: this.toolTypeForm.priority,
              icon: selectedIcon ? selectedIcon.html : "",
            };

            const response = await this.$http.addToolType(formData);
            if (response.code === 200) {
              this.$message.success("");
              this.dialogVisible = false;
              this.resetForm();
              this.loadToolTypeList();
            } else {
              this.$message.error(response.message || "");
            }
          } catch (error) {
            this.$message.error("");
          }
        }
      });
    },

    // 
    openDeleteDialog(toolType) {
      this.currentToolType = toolType;
      this.deleteToolTypeVisible = true;
    },

    // 
    async confirmToolTypeDelete() {
      try {
        const response = await this.$http.deleteToolType(
          this.currentToolType.id
        );
        if (response.code === 200) {
          this.$message.success("");
          this.deleteToolTypeVisible = false;
          this.loadToolTypeList();
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        this.$message.error("");
      }
    },

    // 
    formatDate(dateString) {
      if (!dateString) return "";
      const date = new Date(dateString);
      return date.toLocaleDateString() + " " + date.toLocaleTimeString();
    },
  },
};
</script>

<style scoped>
.toolbar {
  margin-bottom: 20px;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
}
</style>