import { createWebHistory, createRouter } from 'vue-router';

import HomeView from './components/Home.vue';
import UserManagement from './components/UserManagement.vue';
import RoleManagement from './components/RoleManagement.vue';
import PasswordManagement from './components/PasswordManagement.vue';
import BannerManagement from './components/BannerManagement.vue';
import LinkManagement from './components/LinkManagement.vue';
import ToolManagement from './components/ToolManagement.vue';
import ToolType from './components/ToolType.vue';
import AnnouncementList from './components/AnnouncementList.vue';
import AnnouncementAdd from './components/AnnouncementAdd.vue';


const routes = [
    { path: '/', component: HomeView, name: "home" },
    { path: '/users', component: UserManagement, name: "users" },
    { path: '/roles', component: RoleManagement, name: "roles" },
    { path: '/password', component: PasswordManagement, name: "password" },
    { path: '/banners', component: BannerManagement, name: "banners" },
    { path: '/links', component: LinkManagement, name: "links" },
    { path: '/tools', 
      name: "tools",
      redirect: '/tools/list',
      children: [
        { path: 'list', component: ToolManagement, name: "toolmanagement" },
        { path: 'type', component: ToolType, name: "tooltype" }
      ]
    },
    {
      path: '/announcements',
      name: "announcements",
      redirect: '/announcements/list',
      children: [
        { path: 'list', component: AnnouncementList, name: "announcelist" },
        { path: 'add', component: AnnouncementAdd, name: "announceadd" }
      ]
    }
]

const router = createRouter({
    history: createWebHistory(),
    routes,
})

export default router;