<template>
  <h1></h1>
  <div class="home-view">
    <div class="lines">
      <div class="line-web-access" id="webaccess"></div>
    </div>

    <div class="pies">
      <div class="pie-tools" id="tools"></div>
    </div>

    <div class="gauges">
      <h2 class="gauge-title"></h2>
      <div class="gauge-cpu" id="cpu"></div>
      <div class="gauge-memory" id="memory"></div>
    </div>

    <div class="bars">
      <div class="bar-access-announce" id="announces"></div>
    </div>
  </div>
</template>
<script>
import * as echarts from "echarts";
import { ElMessage } from "element-plus";

export default {
  name: "HomeView",
  mounted() {
    this.initWebAccess();
    this.initGaugeCPU();
    this.initGaugeMEM();
    this.initPieTools();
    this.initBarAccessAnnounce();
  },
  methods: {
    initWebAccess() {
      this.$http.getWebAccessInfo().then((res) => {
        var xAxis_data = res.data.xAxis_data;
        var yAxis_data = res.data.yAxis_data;
        var chartDom = document.getElementById("webaccess");
        var myChart = echarts.init(chartDom);
        var option;

        option = {
          title: {
            text: "",
          },
          tooltip: {
            trigger: "axis",
          },
          xAxis: {
            type: "category",
            data: xAxis_data,
          },
          yAxis: {
            type: "value",
          },
          series: [
            {
              data: yAxis_data,
              type: "line",
              smooth: true,
            },
          ],
        };

        option && myChart.setOption(option);
      });
    },
    initGaugeCPU() {
      this.$http.getSysInfo().then((res) => {
        var Cpu_usage = res.data.cpu_usage;
        var chartDom = document.getElementById("cpu");
        var myChart = echarts.init(chartDom);
        var option;

        option = {
          tooltip: {
            formatter: "{a} <br/>{b} : {c}%",
          },
          series: [
            {
              name: "Pressure",
              type: "gauge",
              progress: {
                show: true,
              },
              detail: {
                valueAnimation: true,
                formatter: "{value}",
              },
              data: [
                {
                  value: Cpu_usage,
                  name: "CPU",
                },
              ],
            },
          ],
        };
        option && myChart.setOption(option);
      });
    },
    initGaugeMEM() {
      this.$http.getSysInfo().then((res) => {
        var Memory_usage = res.data.mem_usage;
        var chartDom = document.getElementById("memory");
        var myChart = echarts.init(chartDom);
        var option;

        option = {
          tooltip: {
            formatter: "{a} <br/>{b} : {c}%",
          },
          series: [
            {
              name: "Pressure",
              type: "gauge",
              progress: {
                show: true,
              },
              detail: {
                valueAnimation: true,
                formatter: "{value}",
              },
              data: [
                {
                  value: Memory_usage,
                  name: "",
                },
              ],
            },
          ],
        };
        option && myChart.setOption(option);
      });
    },
    initPieTools() {
      this.$http.getToolMemu().then((res) => {
        if (res["code"] != 200) {
          ElMessage.error(res["message"]);
          return;
        }
        var tool_data = res.data;
        var chartDom = document.getElementById("tools");
        var myChart = echarts.init(chartDom);
        var option;

        option = {
          title: {
            text: "",
            left: "center",
          },
          tooltip: {
            trigger: "item",
          },
          legend: {
            orient: "vertical",
            left: "left",
          },
          series: [
            {
              name: "",
              type: "pie",
              radius: "50%",
              data: tool_data,
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)",
                },
              },
            },
          ],
        };

        option && myChart.setOption(option);
      });
    },
    initBarAccessAnnounce() {
      this.$http.getToolUsageCount().then((res) => {
        var xAxis_data = res.data.xAxis_data;
        var yAxis_data = res.data.yAxis_data;
        var chartDom = document.getElementById("announces");
        var myChart = echarts.init(chartDom);
        var option;

        option = {
          title: {
            text: "",
          },
          tooltip: {
            trigger: "axis",
          },
          xAxis: {
            type: "category",
            data: xAxis_data,
            axisLabel: {
              rotate: 45, // 
              interval: 0, // 
            },
          },
          yAxis: {
            type: "value",
          },
          series: [
            {
              data: yAxis_data,
              type: "bar",
            },
          ],
        };

        option && myChart.setOption(option);
      });
    },
  },
};
</script>
<style scoped>
.home-view {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
}

.lines {
  grid-column: 1 / -1;
  background: white;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.pies {
  background: white;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.gauges {
  display: flex;
  flex-direction: column;
  grid-row: 2;
  background: white;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.gauge-title {
  margin-top: 0;
  text-align: center;
}

.gauge-cpu,
.gauge-memory {
  flex: 1;
  min-height: 150px;
}

.bars {
  grid-column: 1 / -1;
  background: white;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

/*  */
@media (max-width: 768px) {
  .home-view {
    grid-template-columns: 1fr;
  }
  
  .pies,
  .gauges {
    grid-column: 1;
  }
}

.line-web-access,
.pie-tools,
.gauge-cpu,
.gauge-memory,
.bar-access-announce {
  width: 100%;
  height: 100%;
  min-height: 300px;
}
</style>