<!-- AnnouncementAdd.vue -->
<template>
  <div class="announcement-add">
    <el-card class="announcement-card">
      <template #header>
        <div class="card-header">
          <span></span>
        </div>
      </template>

      <el-form
        :model="announcementForm"
        :rules="formRules"
        ref="announcementFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="title">
          <el-input
            v-model="announcementForm.title"
            placeholder=""
            maxlength="100"
            show-word-limit
          />
        </el-form-item>

        <el-form-item label="" prop="content">
          <div style="border: 1px solid #ccc">
            <Toolbar
              style="border-bottom: 1px solid #ccc"
              :editor="editorRef"
              :defaultConfig="toolbarConfig"
              :mode="mode"
            />
            <Editor
              style="height: 500px; overflow-y: hidden;"
              v-model="announcementForm.content"
              :defaultConfig="editorConfig"
              :mode="mode"
              @onCreated="handleCreated"
            />
          </div>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm"></el-button>
          <el-button @click="resetForm"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import "@wangeditor/editor/dist/css/style.css"; //  css
import { onBeforeUnmount, shallowRef } from "vue";
import { Editor, Toolbar } from "@wangeditor/editor-for-vue";
// import { DomEditor } from "@wangeditor/editor";

export default {
  name: "AnnouncementAdd",
  components: { Editor, Toolbar },
  setup() {
    //  shallowRef
    const editorRef = shallowRef();

    const toolbarConfig = {};
    toolbarConfig.excludeKeys = [
        'group-image',
        'group-video',
        'todo'
    ]
    const editorConfig = { placeholder: "..." };

    // 
    onBeforeUnmount(() => {
      const editor = editorRef.value;
      if (editor == null) return;
      editor.destroy();
    });

    const handleCreated = (editor) => {
      editorRef.value = editor; //  editor 

    //   setTimeout(() => {
    //     const toolbar = DomEditor.getToolbar(editor);
    //     if (toolbar) {
    //       const curToolbarConfig = toolbar.getConfig();
    //       console.log(":", curToolbarConfig);
    //       console.log(":", curToolbarConfig.toolbarKeys);
    //     } else {
    //       console.log("");
    //     }
    //   }, 100);
    };

    return {
      editorRef,
      mode: "default", //  'simple'
      toolbarConfig,
      editorConfig,
      handleCreated,
    };
  },
  data() {
    return {
      announcementForm: {
        title: "",
        content: "",
      },
      formRules: {
        title: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 100,
            message: " 1  100 ",
            trigger: "blur",
          },
        ],
        content: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      announcementFormRef: null,
    };
  },
  methods: {
    submitForm() {
      this.$refs.announcementFormRef.validate(async (valid) => {
        if (valid) {
          try {
            // API
            const response = await this.$http.addAnnounce(this.announcementForm);
            if (response.code === 200) {
              this.$message.success('');
              this.resetForm();
              this.$router.push({ name: 'announcelist' });
            } else {
              this.$message.error(response.message || '');
            }
          } catch (error) {
            console.error(":", error);
            this.$message.error("");
          }
        }
      });
    },

    resetForm() {
      this.$refs.announcementFormRef.resetFields();
      // 
      this.announcementForm.content = "";
    },
  },
};
</script>

<style scoped>
.announcement-add {
  padding: 20px;
}

.announcement-card {
  max-width: 87%;
  margin: 0 auto;
}

.card-header {
  font-size: 18px;
  font-weight: bold;
}

.el-form-item {
  margin-bottom: 20px;
}

.el-button {
  margin-right: 10px;
}
</style>