<!-- ToolManagement.vue -->
<template>
  <div class="tool-management">
    <!--  -->
    <div class="toolbar">
      <el-button type="primary" @click="openAddDialog"></el-button>
    </div>

    <!--  -->
    <el-table :data="toolList" style="width: 100%" v-loading="loading">
      <el-table-column prop="name" label="" />
      <el-table-column
        prop="description"
        label=""
        show-overflow-tooltip
      />
      <el-table-column label="">
        <template #default="scope">
          <el-image
            v-if="scope.row.icon_url"
            :src="$http.server_host + scope.row.icon_url"
            fit="cover"
            style="width: 40px; height: 40px"
          />
        </template>
      </el-table-column>
      <el-table-column prop="link_url" label="" show-overflow-tooltip>
        <template #default="scope">
          <a :href="scope.row.link_url" target="_blank">{{ scope.row.link_url }}</a>
        </template>
      </el-table-column>
      <el-table-column prop="type.name" label="" />
      <el-table-column label="" width="200">
        <template #default="scope">
          <el-button size="small" @click="openEditDialog(scope.row)"
            ></el-button
          >
          <el-button
            size="small"
            type="danger"
            @click="openDeleteDialog(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <el-pagination
        v-if="total > 0"
        @current-change="handlePageChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="total"
      />
    </div>

    <!-- / -->
    <el-dialog
      :title="dialogTitle"
      v-model="dialogVisible"
      width="500px"
      @close="resetForm"
    >
      <el-form
        :model="toolForm"
        :rules="formRules"
        ref="toolFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="toolForm.name" placeholder="" />
        </el-form-item>

        <el-form-item label="" prop="description">
          <el-input
            v-model="toolForm.description"
            type="textarea"
            placeholder=""
            :rows="3"
          />
        </el-form-item>

        <el-form-item label="" prop="icon_url">
          <div class="image-upload-container">
            <el-input
              v-model="toolForm.icon_url"
              placeholder="URL"
              style="margin-right: 10px"
            />
            <el-upload
              class="upload-demo"
              :action="$http.server_host + '/cmsapi/tool/icon/upload'"
              :headers="uploadHeaders"
              name="icon_url"
              :show-file-list="false"
              :on-success="handleUploadSuccess"
              :on-error="handleUploadError"
              :before-upload="beforeUpload"
              accept="image/jpeg,image/png,image/jpg,image/svg+xml"
            >
              <el-button type="primary" size="small"></el-button>
            </el-upload>
          </div>
        </el-form-item>

        <el-form-item label="" prop="link_url">
          <el-input v-model="toolForm.link_url" placeholder="" />
        </el-form-item>

        <el-form-item label="" prop="type_name">
          <el-select
            v-model="toolForm.type_name"
            placeholder=""
            style="width: 100%"
          >
            <el-option
              v-for="type in toolTypeList"
              :key="type.name"
              :label="type.name"
              :value="type.name"
            />
          </el-select>
        </el-form-item>
      </el-form>

      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="submitForm"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog title="" v-model="deleteToolVisible" width="300px" center>
      <span></span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="deleteToolVisible = false"></el-button>
          <el-button type="danger" @click="confirmDelete"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ToolManagement",
  data() {
    return {
      loading: false,
      toolList: [],
      toolTypeList: [],
      total: 0,
      currentPage: 1,
      pageSize: 10,
      dialogVisible: false,
      deleteToolVisible: false,
      dialogTitle: "",
      currentTool: null,
      toolForm: {
        name: "",
        description: "",
        icon_url: "",
        link_url: "",
        type_name: "",
      },
      formRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        description: [
          { required: true, message: "", trigger: "blur" },
        ],
        icon_url: [
          { required: true, message: "", trigger: "blur" },
        ],
        link_url: [
          { required: true, message: "", trigger: "blur" },
        ],
        type_name: [
          { required: true, message: "", trigger: "change" },
        ],
      },
    };
  },
  computed: {
    // 
    uploadHeaders() {
      return {
        Authorization: "Bearer " + this.$auth.token,
      };
    },
  },
  mounted() {
    this.loadToolList();
    this.loadToolTypeList();
  },
  methods: {
    // 
    async loadToolList() {
      this.loading = true;
      try {
        const response = await this.$http.getToolList(this.currentPage);
        if (response.code === 200) {
          this.toolList = response.data.tool_list || [];
          this.total = response.data.total_count || 0;
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error("");
      } finally {
        this.loading = false;
      }
    },

    // 
    async loadToolTypeList() {
      try {
        const response = await this.$http.getToolTypeList();
        if (response.code === 200) {
          if (response.data.length > 0) {
            this.toolTypeList = response.data;
          } else {
            this.toolTypeList = [];
          }
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error("");
      }
    },

    // 
    handlePageChange(page) {
      this.currentPage = page;
      this.loadToolList();
    },

    // 
    openAddDialog() {
      this.dialogTitle = "";
      this.currentTool = null;
      this.dialogVisible = true;
    },

    // 
    openEditDialog(tool) {
      this.dialogTitle = "";
      this.currentTool = { ...tool };
      console.log(tool);
      this.toolForm = {
        name: tool.name,
        description: tool.description,
        icon_url: tool.icon_url,
        link_url: tool.link_url,
        type_name: tool.type?.name || "",
      };
      this.dialogVisible = true;
    },

    // 
    resetForm() {
      this.toolForm = {
        name: "",
        description: "",
        icon_url: "",
        link_url: "",
        type_name: "",
      };
      this.currentTool = null;
      if (this.$refs.toolFormRef) {
        this.$refs.toolFormRef.resetFields();
      }
    },

    // 
    submitForm() {
      this.$refs.toolFormRef.validate(async (valid) => {
        if (valid) {
          try {
            let response;
            if (this.currentTool) {
              // 
              response = await this.$http.updateTool({
                ...this.toolForm,
                id: this.currentTool.id,
              });
            } else {
              // 
              response = await this.$http.addTool(this.toolForm);
            }

            if (response.code === 200) {
              this.$message.success(this.currentTool ? "" : "");
              this.dialogVisible = false;
              this.resetForm();
              this.loadToolList();
            } else {
              this.$message.error(
                response.message || (this.currentTool ? "" : "")
              );
            }
          } catch (error) {
            console.error(":", error);
            this.$message.error("");
          }
        }
      });
    },

    // 
    openDeleteDialog(tool) {
      this.currentTool = tool;
      this.deleteToolVisible = true;
    },

    // 
    async confirmDelete() {
      try {
        const response = await this.$http.deleteTool(this.currentTool.id);
        if (response.code === 200) {
          this.$message.success("");
          this.deleteToolVisible = false;
          this.loadToolList();
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error("");
      }
    },

    // 
    beforeUpload(file) {
      const isImage = file.type.startsWith("image/");
      const isLt10M = file.size / 1024 / 1024 < 10;

      if (!isImage) {
        this.$message.error("!");
      }
      if (!isLt10M) {
        this.$message.error(" 10MB!");
      }
      return isImage && isLt10M;
    },

    // 
    handleUploadSuccess(response) {
      console.log(":", response);
      if (response.code === 200) {
        this.toolForm.icon_url = response.data.icon_url;
        this.$message.success("");
      } else {
        this.$message.error(response.message || "");
      }
    },

    // 
    handleUploadError(error) {
      this.$message.error("");
      console.error(":", error);
    },
  },
};
</script>

<style scoped>
.toolbar {
  margin-bottom: 20px;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
}

.image-upload-container {
  display: flex;
  width: 100%;
}

.upload-demo {
  display: inline-block;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
</style>