import axios from "axios"
import auth from "./auth"
import qs from "qs";


class Http {
	constructor() {
		if (process.env.NODE_ENV == 'production') {
			this.server_host = window.location.origin;
		} else {
			this.server_host = "http://127.0.0.1:5000"
		}
		this.http = axios.create({
			baseURL: this.server_host + "/cmsapi",
			timeout: 1000 * 60
		});

		// JWT
		this.http.interceptors.request.use(config => {
			const token = auth.token
			if (token) {
				config.headers.Authorization = "Bearer " + token
			}
			return config
		})

		//
		this.http.interceptors.response.use(response => {
			// 
			return response.data
		})
	}
	_post(url, data) {
		return this.http.post(url, qs.stringify(data));
	}

	addBanner(data) {
		const url = "/banner/add";
		return this._post(url, data);
	}

	getBannerList() {
		const url = "/banner/list";
		return this.http.get(url);
	}

	deleteBanner(id) {
		const url = "/banner/delete";
		return this._post(url, { "id": id });
	}

	updateBanner(data) {
		const url = "/banner/update";
		return this._post(url, data);
	}

	refreshBanner() {
		const url = "/banner/refresh";
		return this.http.get(url);
	}

	getAnnounceList(page) {
		const url = "/announce/list?page=" + (page ? page : 1);
		return this.http.get(url);
	}

	addAnnounce(data) {
		const url = "/announce/add";
		return this._post(url, data);
	}

	deleteAnnounce(id) {
		const url = "/announce/delete";
		return this._post(url, { "id": id });
	}

	activeAnnounce(id, is_active) {
		const url = "/announce/active?id=" + id + "&is_active=" + is_active;
		return this._post(url, { "id": id, "is_active": is_active });
	}

	getCommentList(page) {
		const url = "/comment/list?page=" + (page ? page : 1);
		return this.http.get(url);
	}

	deleteComment(id) {
		const url = "/comment/delete";
		return this._post(url, { "id": id });
	}

	getToolTypeList() {
		const url = "/tooltype/list";
		return this.http.get(url);
	}

	addToolType(data) {
		const url = "/tooltype/add";
		return this._post(url, data);
	}

	deleteToolType(id) {
		const url = "/tooltype/delete";
		return this._post(url, { "id": id });
	}

	getToolList(page) {
		const url = "/tool/list?page=" + (page ? page : 1);
		return this.http.get(url);
	}

	addTool(data) {
		const url = "/tool/add";
		return this._post(url, data);
	}
	deleteTool(id) {
		const url = "/tool/delete";
		return this._post(url, { "id": id });
	}
	updateTool(data) {
		const url = "/tool/update";
		return this._post(url, data);
	}

	getLinkList(page) {
		const url = "/link/list?page=" + (page ? page : 1);
		return this.http.get(url);
	}

	addLink(data) {
		const url = "/link/add";
		return this._post(url, data);
	}
	deleteLink(id) {
		const url = "/link/delete";
		return this._post(url, { "id": id });
	}
	updateLink(data) {
		const url = "/link/update";
		return this._post(url, data);
	}
	activeLink(id, is_deployed) {
		const url = "/link/deploy/status?id=" + id + "&is_deployed=" + is_deployed;
		return this._post(url, { "id": id, "is_deployed": is_deployed });
	}
	getUserList(page) {
		const url = "/user/list?page=" + (page ? page : 1);
		return this.http.get(url);
	}

	activeUser(id, is_active) {
		const url = "/user/active?id=" + id + "&is_active=" + is_active;
		return this._post(url, { "id": id, "is_active": is_active });
	}
	addUser(data) {
		const url = "/user/add";
		return this._post(url, data);
	}
	deleteUser(id) {
		const url = "/user/delete";
		return this._post(url, { "id": id });
	}

	getRoleList() {
		const url = "/role/list";
		return this.http.get(url);
	}

	updateUserRole(data) {
		const url = "/user/update/role";
		return this._post(url, data);
	}

	updateUserPassword(data) {
		const url = "/user/update/password";
		return this._post(url, data);
	}

	resetUserPassword(data) {
		const url = "/user/reset/password";
		return this._post(url, data);
	}

	getSysInfo() {
		const url = "/sys/useage";
		return this.http.get(url);
	}

	getToolMemu() {
		const url = "/tool/menu";
		return this.http.get(url);
	}
	
	getToolUsageCount() {
		const url = "/tool/usage/count";
		return this.http.get(url);
	}

	getWebAccessInfo(){
		const url = "/web/access/info";
		return this.http.get(url);
	}
}

export default new Http()