<!-- RoleManagement.vue -->
<template>
  <div class="role-management">
    <el-card class="role-card">
      <template #header>
        <div class="card-header">
          <span></span>
        </div>
      </template>

      <!--  -->
      <el-table 
        :data="userList" 
        style="width: 100%" 
        v-loading="loading"
        stripe
      >
        <el-table-column prop="username" label="" width="150" />
        <el-table-column prop="realname" label="" width="150" />
        <el-table-column prop="role.name" label="" width="150">
          <template #default="scope">
            <el-tag :type="getRoleTagType(scope.row.role)">
              {{ scope.row.role.name }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template #default="scope">
            <el-tag :type="scope.row.is_active ? 'success' : 'danger'">
              {{ scope.row.is_active ? '' : '' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="join_time" label="" width="200">
          <template #default="scope">
            {{ formatDate(scope.row.join_time) }}
          </template>
        </el-table-column>
        <el-table-column label="" width="150">
          <template #default="scope">
            <el-button
              v-if="canModifyUserRole(scope.row)"
              size="small"
              type="primary"
              @click="openModifyRoleDialog(scope.row)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div class="pagination-container">
        <el-pagination
          v-if="total > 0"
          @current-change="handlePageChange"
          :current-page="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="total"
        />
      </div>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      v-model="modifyRoleDialogVisible"
      width="500px"
      @close="resetModifyRoleForm"
    >
      <el-form
        :model="modifyRoleForm"
        :rules="modifyRoleRules"
        ref="modifyRoleFormRef"
        label-width="100px"
      >
        <el-form-item label="">
          <el-input v-model="currentUser.username" disabled />
        </el-form-item>
        
        <el-form-item label="" prop="role_id">
          <el-select 
            v-model="modifyRoleForm.role_id" 
            placeholder="" 
            style="width: 100%"
          >
            <el-option
              v-for="role in getAvailableRoles"
              :key="role.id"
              :label="role.name"
              :value="role.id"
            />
          </el-select>
        </el-form-item>
      </el-form>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="modifyRoleDialogVisible = false"></el-button>
          <el-button type="primary" @click="submitModifyRole"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "RoleManagement",
  data() {
    return {
      loading: false,
      userList: [],
      roleList: [],
      total: 0,
      currentPage: 1,
      pageSize: 10,
      modifyRoleDialogVisible: false,
      currentUser: {},
      modifyRoleForm: {
        role_id: ''
      },
      modifyRoleRules: {
        role_id: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    };
  },
  computed: {
    // 
    currentLoginUser() {
      return this.$auth.user;
    },
    
    // 
    currentUserRoleLevel() {
      return this.currentLoginUser?.role?.permissions || 0;
    },
    
    // 
    maxRoleLevel() {
      if (this.roleList.length === 0) return 0;
      return Math.max(...this.roleList.map(role => role.permissions));
    },
    
    // 
    secondMaxRoleLevel() {
      if (this.roleList.length < 2) return 0;
      const permissions = [...new Set(this.roleList.map(role => role.permissions))]
        .sort((a, b) => b - a);
      return permissions.length > 1 ? permissions[1] : permissions[0];
    },
    
    // 
    getAvailableRoles() {
      // 
      if (this.currentUserRoleLevel === this.maxRoleLevel) {
        return this.roleList.filter(role => role.permissions < this.maxRoleLevel);
      }
      
      // 
      if (this.currentUserRoleLevel === this.secondMaxRoleLevel) {
        return this.roleList.filter(role => role.permissions < this.maxRoleLevel);
      }
      
      // 
      return [];
    }
  },
  mounted() {
    this.loadUserList();
    this.loadRoleList();
  },
  methods: {
    // 
    async loadUserList() {
      this.loading = true;
      try {
        const response = await this.$http.getUserList(this.currentPage);
        if (response.code === 200) {
          this.userList = response.data.user_list || [];
          this.total = response.data.total_count || 0;
        } else {
          this.$message.error(response.message || '');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      } finally {
        this.loading = false;
      }
    },
    
    // 
    async loadRoleList() {
      try {
        const response = await this.$http.getRoleList();
        if (response.code === 200) {
          this.roleList = response.data || [];
        } else {
          this.$message.error(response.message || '');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },
    
    // 
    handlePageChange(page) {
      this.currentPage = page;
      this.loadUserList();
    },
    
    // 
    formatDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
    },
    
    // 
    getRoleTagType(role) {
      if (!role) return 'info';
      
      const roleLevel = role.permissions;
      
      if (roleLevel === this.maxRoleLevel) {
        return 'danger'; // 
      } else if (roleLevel === this.secondMaxRoleLevel) {
        return 'warning'; // 
      } else if (roleLevel === Math.min(...this.roleList.map(r => r.permissions))) {
        return 'info'; // 
      } else {
        return 'primary'; // 
      }
    },
    
    // 
    canModifyUserRole(user) {
      // 
      if (user.id === this.currentLoginUser.id) {
        return false;
      }
      
      const userRoleLevel = user.role?.permissions || 0;
      
      // 
      if (this.currentUserRoleLevel === this.maxRoleLevel) {
        return true;
      }
      
      // 
      if (this.currentUserRoleLevel === this.secondMaxRoleLevel) {
        return userRoleLevel < this.secondMaxRoleLevel;
      }
      
      // 
      return false;
    },
    
    // 
    openModifyRoleDialog(user) {
      this.currentUser = { ...user };
      this.modifyRoleForm.role_id = user.role.id;
      this.modifyRoleDialogVisible = true;
    },
    
    // 
    resetModifyRoleForm() {
      this.currentUser = {};
      this.modifyRoleForm = {
        role_id: ''
      };
      if (this.$refs.modifyRoleFormRef) {
        this.$refs.modifyRoleFormRef.resetFields();
      }
    },
    
    // 
    submitModifyRole() {
      this.$refs.modifyRoleFormRef.validate(async (valid) => {
        if (valid) {
          try {
            const formData = {
              id: this.currentUser.id,
              role_id: this.modifyRoleForm.role_id
            };
            
            const response = await this.$http.updateUserRole(formData);
            if (response.code === 200) {
              this.$message.success('');
              this.modifyRoleDialogVisible = false;
              this.resetModifyRoleForm();
              this.loadUserList();
            } else {
              this.$message.error(response.message || '');
            }
          } catch (error) {
            console.error(':', error);
            this.$message.error('');
          }
        }
      });
    }
  }
};
</script>

<style scoped>
.role-management {
  padding: 20px;
}

.role-card {
  max-width: 100%;
}

.card-header {
  font-size: 18px;
  font-weight: bold;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
}

.dialog-footer .el-button {
  margin-left: 10px;
}
</style>