<!-- PasswordManagement.vue -->
<template>
  <div class="password-management">
    <el-card class="user-info-card">
      <template #header>
        <div class="card-header">
          <span></span>
        </div>
      </template>
      
      <el-descriptions :column="1" border>
        <el-descriptions-item label="">{{ currentUser.username || '-' }}</el-descriptions-item>
        <el-descriptions-item label="">{{ currentUser.realname || '-' }}</el-descriptions-item>
        <el-descriptions-item label="">{{ formatDate(currentUser.join_time) || '-' }}</el-descriptions-item>
        <el-descriptions-item label="">
          <el-tag :type="currentUser.is_active ? 'success' : 'danger'" v-if="currentUser.is_active !== undefined">
            {{ currentUser.is_active ? '' : '' }}
          </el-tag>
          <span v-else>-</span>
        </el-descriptions-item>
        <el-descriptions-item label="">{{ currentUser.role?.name || '-' }}</el-descriptions-item>
      </el-descriptions>
      
      <div class="button-group">
        <el-button type="primary" @click="openChangePasswordDialog" :disabled="!currentUser.id"></el-button>
        <el-button 
          type="warning" 
          @click="openResetPasswordDialog"
          v-if="hasPermission(62)"
        >
          
        </el-button>
      </div>
    </el-card>
    
    <!--  -->
    <el-dialog
      title=""
      v-model="changePasswordDialogVisible"
      width="500px"
      @close="resetChangePasswordForm"
    >
      <el-form
        :model="changePasswordForm"
        :rules="changePasswordRules"
        ref="changePasswordFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="old_password">
          <el-input
            v-model="changePasswordForm.old_password"
            type="password"
            placeholder=""
            show-password
          />
        </el-form-item>
        
        <el-form-item label="" prop="new_password">
          <el-input
            v-model="changePasswordForm.new_password"
            type="password"
            placeholder=""
            show-password
          />
        </el-form-item>
        
        <el-form-item label="" prop="repeat_password">
          <el-input
            v-model="changePasswordForm.repeat_password"
            type="password"
            placeholder=""
            show-password
          />
        </el-form-item>
      </el-form>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="changePasswordDialogVisible = false"></el-button>
          <el-button type="primary" @click="submitChangePassword"></el-button>
        </span>
      </template>
    </el-dialog>
    
    <!--  -->
    <el-dialog
      title=""
      v-model="resetPasswordDialogVisible"
      width="500px"
      @close="resetResetPasswordForm"
    >
      <el-form
        :model="resetPasswordForm"
        :rules="resetPasswordRules"
        ref="resetPasswordFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="username">
          <el-input 
            v-model="resetPasswordForm.username" 
            placeholder=""
            style="width: 100%"
          />
        </el-form-item>
      </el-form>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="resetPasswordDialogVisible = false"></el-button>
          <el-button type="warning" @click="submitResetPassword"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "PasswordManagement",
  data() {
    //  - 
    const validateRepeatPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.changePasswordForm.new_password) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    
    return {
      currentUser: {},
      changePasswordDialogVisible: false,
      resetPasswordDialogVisible: false,
      changePasswordForm: {
        old_password: '',
        new_password: '',
        repeat_password: ''
      },
      resetPasswordForm: {
        username: ''
      },
      changePasswordRules: {
        old_password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        new_password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        repeat_password: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateRepeatPassword, trigger: 'blur' }
        ]
      },
      resetPasswordRules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    };
  },
  mounted() {
    this.loadCurrentUser();
  },
  methods: {
    // 
    loadCurrentUser() {
      this.currentUser = this.$auth.user || {};
    },
    
    // 
    hasPermission(requiredPermission) {
      return this.$auth.user?.role?.permissions >= requiredPermission;
    },
    
    // 
    formatDate(dateString) {
      if (!dateString) return '-';
      const date = new Date(dateString);
      return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
    },
    
    // 
    openChangePasswordDialog() {
      this.changePasswordDialogVisible = true;
    },
    
    // 
    openResetPasswordDialog() {
      this.resetPasswordDialogVisible = true;
    },
    
    // 
    resetChangePasswordForm() {
      this.changePasswordForm = {
        old_password: '',
        new_password: '',
        repeat_password: ''
      };
      if (this.$refs.changePasswordFormRef) {
        this.$refs.changePasswordFormRef.resetFields();
      }
    },
    
    // 
    resetResetPasswordForm() {
      this.resetPasswordForm = {
        username: ''
      };
      if (this.$refs.resetPasswordFormRef) {
        this.$refs.resetPasswordFormRef.resetFields();
      }
    },
    
    // 
    submitChangePassword() {
      this.$refs.changePasswordFormRef.validate(async (valid) => {
        if (valid) {
          try {
            const formData = {
              id: this.currentUser.id,
              old_password: this.changePasswordForm.old_password,
              new_password: this.changePasswordForm.new_password,
              repeat_password: this.changePasswordForm.repeat_password
            };
            
            const response = await this.$http.updateUserPassword(formData);
            if (response.code === 200) {
              this.$message.success('');
              this.changePasswordDialogVisible = false;
              this.resetChangePasswordForm();
            } else {
              this.$message.error(response.message || '');
            }
          } catch (error) {
            console.error(':', error);
            this.$message.error('');
          }
        }
      });
    },
    
    // 
    submitResetPassword() {
      this.$refs.resetPasswordFormRef.validate(async (valid) => {
        if (valid) {
          try {
            const formData = {
              username: this.resetPasswordForm.username,
              new_password: '111111'
            };
            
            // 
            // 
            const response = await this.$http.resetUserPassword(formData);
            if (response.code === 200) {
              this.$message.success('111111');
              this.resetPasswordDialogVisible = false;
              this.resetResetPasswordForm();
            } else {
              this.$message.error(response.message || '');
            }
          } catch (error) {
            console.error(':', error);
            this.$message.error('');
          }
        }
      });
    }
  }
};
</script>

<style scoped>
.password-management {
  padding: 20px;
}

.user-info-card {
  max-width: 800px;
  margin: 0 auto;
}

.card-header {
  font-size: 18px;
  font-weight: bold;
}

.button-group {
  margin-top: 20px;
  text-align: center;
}

.button-group .el-button {
  margin: 0 10px;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
}
</style>