<!-- AnnouncementList.vue -->
<template>
  <div class="announcement-list">
    <el-card class="announcement-card">
      <template #header>
        <div class="card-header">
          <span></span>
        </div>
      </template>

      <el-table 
        :data="announcementList" 
        v-loading="loading"
        style="width: 100%"
        stripe
      >
        <el-table-column prop="title" label="" width="200" />
        
        <el-table-column label="" min-width="300">
          <template #default="scope">
            <div class="content-preview text-muted">
              {{ scope.row.text_muted }}
            </div>
          </template>
        </el-table-column>
        
        <el-table-column prop="author.realname" label="" width="120" />
        
        <el-table-column label="" width="100">
          <template #default="scope">
            <el-tag :type="scope.row.is_active ? 'success' : 'danger'">
              {{ scope.row.is_active ? '' : '' }}
            </el-tag>
          </template>
        </el-table-column>
        
        <el-table-column label="" width="200">
          <template #default="scope">
            <el-button 
              size="small" 
              :type="scope.row.is_active ? 'warning' : 'success'"
              @click="toggleStatus(scope.row)"
            >
              {{ scope.row.is_active ? '' : '' }}
            </el-button>
            
            <el-button 
              size="small" 
              type="danger" 
              @click="openDeleteDialog(scope.row)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      
      <div class="pagination-container">
        <el-pagination
          v-if="total > 0"
          @current-change="handlePageChange"
          :current-page="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="total"
        />
      </div>
    </el-card>
    
    <!--  -->
    <el-dialog
      title=""
      v-model="deleteDialogVisible"
      width="30%"
      center
    >
      <div class="delete-confirm-content">
        <el-icon class="delete-icon"><Warning /></el-icon>
        <span></span>
      </div>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="deleteDialogVisible = false"></el-button>
          <el-button type="danger" @click="confirmDelete"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'AnnouncementList',
  data() {
    return {
      loading: false,
      announcementList: [],
      total: 0,
      currentPage: 1,
      pageSize: 10,
      deleteDialogVisible: false,
      currentAnnouncement: null
    }
  },
  mounted() {
    this.loadAnnouncementList()
  },
  methods: {
    async loadAnnouncementList() {
      this.loading = true
      try {
        const response = await this.$http.getAnnounceList(this.currentPage)
        if (response.code === 200) {
          this.announcementList = response.data.announce_list || []
          this.total = response.data.total_count || 0
        } else {
          this.$message.error(response.message || '')
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      } finally {
        this.loading = false
      }
    },
    
    handlePageChange(page) {
      this.currentPage = page
      this.loadAnnouncementList()
    },
    
    toggleStatus(announcement) {
        let status = announcement.is_active?0:1
      this.$http.activeAnnounce(announcement.id, status)
        .then(response => {
            console.log(':', response)
          if (response.code === 200) {
            this.$message.success(`${!announcement.is_active ? '' : ''}`)
            this.loadAnnouncementList()
          } else {
            this.$message.error(response.message || `${!announcement.is_active ? '' : ''}`)
          }
        })
        .catch(error => {
          console.error(':', error)
          this.$message.error('')
        })
    },
    
    openDeleteDialog(announcement) {
      this.currentAnnouncement = announcement
      this.deleteDialogVisible = true
    },
    
    async confirmDelete() {
      try {
        const response = await this.$http.deleteAnnounce(this.currentAnnouncement.id)
        if (response.code === 200) {
          this.$message.success('')
          this.deleteDialogVisible = false
          this.loadAnnouncementList()
        } else {
          this.$message.error(response.message || '')
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    }
  }
}
</script>

<style scoped>
.announcement-list {
  padding: 20px;
}

.announcement-card {
  max-width: 100%;
}

.card-header {
  font-size: 18px;
  font-weight: bold;
}

.text-muted {
  color: #6c757d;
}

.content-preview {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.delete-confirm-content {
  display: flex;
  align-items: center;
  justify-content: center;
}

.delete-icon {
  font-size: 24px;
  color: #F56C6C;
  margin-right: 10px;
}

.dialog-footer {
  display: flex;
  justify-content: center;
}

.dialog-footer .el-button {
  margin: 0 10px;
}
</style>