<!-- BannerManagement.vue -->
<template>
  <div class="banner-management">
    <!-- / -->
    <div class="toolbar">
      <el-button type="primary" @click="openAddDialog"></el-button>
    </div>

    <!--  -->
    <el-table :data="bannerList" style="width: 100%" v-loading="loading">
      <el-table-column prop="name" label="" />
      <el-table-column prop="image_url" label="URL">
        <template #default="scope">
          <el-image
            v-if="scope.row.image_url"
            :src="this.$http.server_host + scope.row.image_url"
            fit="cover"
            style="width: 100px; height: 60px"
            :preview-src-list="[scope.row.image_url]"
          />
        </template>
      </el-table-column>
      <el-table-column prop="link_url" label="" show-overflow-tooltip />
      <el-table-column label="" width="200">
        <template #default="scope">
          <el-button size="small" @click="openEditDialog(scope.row)"
            ></el-button
          >
          <el-button
            size="small"
            type="danger"
            @click="openDeleteDialog(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!-- / -->
    <el-dialog
      :title="dialogTitle"
      v-model="dialogVisible"
      width="500px"
      @close="resetForm"
    >
      <el-form
        :model="bannerForm"
        :rules="formRules"
        ref="bannerFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="bannerForm.name" placeholder="" />
        </el-form-item>
        <el-form-item label="URL" prop="image_url">
          <div class="image-upload-container">
            <el-input
              v-model="bannerForm.image_url"
              placeholder="URL"
              style="margin-right: 10px"
            />
            <el-upload
              class="upload-demo"
              :action="uploadUrl"
              :headers="uploadHeaders"
              name="image_url"
              :show-file-list="false"
              :on-success="handleUploadSuccess"
              :on-error="handleUploadError"
              :before-upload="beforeUpload"
              accept="image/jpeg,image/png,image/jpg"
            >
              <el-button type="primary" size="small" style="height: auto"
                ></el-button
              >
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item label="" prop="link_url">
          <el-input
            v-model="bannerForm.link_url"
            placeholder=""
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="submitForm"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog title="" v-model="deleteBannerVisible" width="300px" center>
      <span></span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="deleteBannerVisible = false"></el-button>
          <el-button type="danger" @click="confirmDelete"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "BannerManagement",
  data() {
    return {
      loading: false,
      bannerList: [],
      dialogVisible: false,
      deleteBannerVisible: false,
      dialogTitle: "",
      currentBanner: null,
      bannerForm: {
        name: "",
        image_url: "",
        link_url: "",
      },
      formRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
        ],
        image_url: [
          { required: true, message: "URL", trigger: "blur" },
        ],
        link_url: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  computed: {
    // 
    uploadUrl() {
      return this.$http.server_host + "/cmsapi/banner/upload";
    },
    // 
    uploadHeaders() {
      return {
        Authorization: "Bearer " + this.$auth.token,
      };
    },
  },
  mounted() {
    this.loadBannerList();
  },
  methods: {
    // 
    async loadBannerList() {
      this.loading = true;
      try {
        const response = await this.$http.getBannerList();
        if (response.code === 200) {
          if(response.data.length > 0 ){
            this.bannerList = response.data;
          }else{
            this.bannerList = [];
          }
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error("");
      } finally {
        this.loading = false;
      }
    },

    // 
    openAddDialog() {
      this.dialogTitle = "";
      this.currentBanner = null;
      this.dialogVisible = true;
    },

    // 
    openEditDialog(banner) {
      this.dialogTitle = "";
      this.currentBanner = { ...banner };
      this.bannerForm = { ...banner };
      this.dialogVisible = true;
    },

    // 
    resetForm() {
      this.bannerForm = {
        name: "",
        image_url: "",
        link_url: "",
      };
      this.currentBanner = null;
    },

    // 
    submitForm() {
      this.$refs.bannerFormRef.validate(async (valid) => {
        if (valid) {
          try {
            let response;
            if (this.currentBanner) {
              // 
              response = await this.$http.updateBanner({
                ...this.bannerForm,
                id: this.currentBanner.id,
              });
            } else {
              // 
              response = await this.$http.addBanner(this.bannerForm);
            }

            if (response.code === 200) {
              this.$message.success(
                this.currentBanner ? "" : ""
              );
              this.$http.refreshBanner();
              this.dialogVisible = false;
              this.resetForm();
              this.loadBannerList();
            } else {
              this.$message.error(
                response.message ||
                  (this.currentBanner ? "" : "")
              );
            }
          } catch (error) {
            console.error(":", error);
            this.$message.error("");
          }
        }
      });
    },

    // 
    openDeleteDialog(banner) {
      this.currentBanner = banner;
      this.deleteBannerVisible = true;
    },

    // 
    async confirmDelete() {
      try {
        const response = await this.$http.deleteBanner(this.currentBanner.id);
        if (response.code === 200) {
          this.$message.success("");
          this.$http.refreshBanner();
          this.deleteBannerVisible = false;
          this.loadBannerList();
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error("");
      }
    },

    // 
    beforeUpload(file) {
      const isImage = file.type.startsWith("image/");
      const isLt2M = file.size / 1024 / 1024 < 10;

      if (!isImage) {
        this.$message.error("!");
      }
      if (!isLt2M) {
        this.$message.error(" 10MB!");
      }
      return isImage && isLt2M;
    },

    // 
    handleUploadSuccess(response) {
      if (response.code === 200) {
        this.bannerForm.image_url = response.data.image_url;
        this.$message.success("");
      } else {
        this.$message.error(response.message || "");
      }
    },

    // 
    handleUploadError(error) {
      this.$message.error("");
      console.error(":", error);
    },
  },
};
</script>

<style scoped>
.toolbar {
  margin-bottom: 20px;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
}

.image-upload-container {
  display: flex;
  width: 100%;
}

.upload-demo {
  display: inline-block;
}
</style>