<!-- LinkManagement.vue -->
<template>
  <div class="link-management">
    <!--  -->
    <div class="toolbar">
      <el-button type="primary" @click="openAddDialog"></el-button>
    </div>

    <!--  -->
    <el-table :data="linkList" style="width: 100%" v-loading="loading">
      <el-table-column prop="name" label="" width="200"/>
      <el-table-column prop="link_url" label="" show-overflow-tooltip >
        <template #default="scope">
            <a :href="scope.row.link_url" target="_blank">{{ scope.row.link_url }}</a>
        </template>
      </el-table-column>
      <el-table-column prop="create_time" label="" width="200">
        <template #default="scope">
          {{ formatDate(scope.row.create_time) }}
        </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template #default="scope">
          <el-tag :type="scope.row.is_deployed ? 'success' : 'danger'">
            {{ scope.row.is_deployed ? "" : "" }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" width="280">
        <template #default="scope">
          <el-button
            size="small"
            :type="scope.row.is_deployed ? 'danger' : 'success'"
            @click="toggleDeployStatus(scope.row)"
          >
            {{ scope.row.is_deployed ? "" : "" }}
          </el-button>
          <el-button
            size="small"
            type="success"
            @click="openEditDialog(scope.row)"
            ></el-button
          >
          <el-button
            size="small"
            type="danger"
            @click="openDeleteDialog(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <div class="pagination">
        <el-pagination
          v-if="total > 0"
          @current-change="handlePageChange"
          :current-page="currentPage"
          layout="total, prev, pager, next"
          :total="total"
        />
      </div>
    </div>

    <!-- / -->
    <el-dialog
      :title="dialogTitle"
      v-model="dialogVisible"
      width="500px"
      @close="resetForm"
    >
      <el-form
        :model="linkForm"
        :rules="formRules"
        ref="linkFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="linkForm.name" placeholder="" />
        </el-form-item>
        <el-form-item label="" prop="link_url">
          <el-input v-model="linkForm.link_url" placeholder="" />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="submitForm"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog title="" v-model="deleteLinkVisible" width="300px" center>
      <span></span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="deleteLinkVisible = false"></el-button>
          <el-button type="danger" @click="confirmDelete"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "LinkManagement",
  data() {
    return {
      loading: false,
      linkList: [],
      total: 0,
      currentPage: 1,
      dialogVisible: false,
      deleteLinkVisible: false,
      dialogTitle: "",
      currentLink: null,
      linkForm: {
        name: "",
        link_url: "",
        is_deployed: 1,
      },
      formRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        link_url: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  mounted() {
    this.loadLinkList(1);
  },
  methods: {
    // 
    async loadLinkList(page) {
      this.loading = true;
      try {
        const response = await this.$http.getLinkList(page);
        if (response.code === 200) {
          if (response.data.link_list.length > 0) {
            this.linkList = response.data.link_list;
            this.total = response.data.total_count;
            this.currentPage = page;
          } else {
            this.linkList = [];
            this.total = 0;
            this.currentPage = 1;
          }
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error("");
      } finally {
        this.loading = false;
      }
    },

    // 
    handlePageChange(page) {
      this.loadLinkList(page);
    },

    // 
    openAddDialog() {
      this.dialogTitle = "";
      this.currentLink = null;
      this.dialogVisible = true;
    },

    // 
    openEditDialog(link) {
      this.dialogTitle = "";
      this.currentLink = { ...link };
      this.linkForm = { ...link };
      this.dialogVisible = true;
    },

    // 
    resetForm() {
      this.linkForm = {
        name: "",
        link_url: "",
      };
      this.currentLink = null;
      if (this.$refs.linkFormRef) {
        this.$refs.linkFormRef.resetFields();
      }
    },

    // 
    submitForm() {
      this.$refs.linkFormRef.validate(async (valid) => {
        if (valid) {
          try {
            let response;
            if (this.currentLink) {
              // 
              response = await this.$http.updateLink({
                ...this.linkForm,
                id: this.currentLink.id,
              });
            } else {
              // 
              response = await this.$http.addLink(this.linkForm);
            }

            if (response.code === 200) {
              this.$message.success(this.currentLink ? "" : "");
              this.dialogVisible = false;
              this.resetForm();
              this.loadLinkList();
            } else {
              this.$message.error(
                response.message || (this.currentLink ? "" : "")
              );
            }
          } catch (error) {
            console.error(":", error);
            this.$message.error("");
          }
        }
      });
    },

    // 
    openDeleteDialog(link) {
      this.currentLink = link;
      this.deleteLinkVisible = true;
    },

    // 
    async confirmDelete() {
      try {
        const response = await this.$http.deleteLink(this.currentLink.id);
        if (response.code === 200) {
          this.$message.success("");
          this.deleteLinkVisible = false;
          this.loadLinkList();
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error("");
      }
    },

    // 
    async toggleDeployStatus(link) {
      try {
        const newStatus = link.is_deployed ? 0 : 1;
        const response = await this.$http.activeLink(link.id, newStatus);
        if (response.code === 200) {
          this.$message.success(
            `${!link.is_deployed ? "" : ""}`
          );
          this.loadLinkList();
        } else {
          this.$message.error(
            response.message || `${!link.is_deployed ? "" : ""}`
          );
        }
      } catch (error) {
        console.error(":", error);
        this.$message.error("");
      }
    },

    // 
    formatDate(dateString) {
      if (!dateString) return "";
      const date = new Date(dateString);
      return date.toLocaleDateString() + " " + date.toLocaleTimeString();
    },
  },
};
</script>

<style scoped>
.toolbar {
  margin-bottom: 20px;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
</style>